export function join(dir, source) {
  return `${dir}/${source}`.replace(/\\/g, '/')
}

export function url(...args) {  
  const [f, s] = args
  if (!f) {
    return `url('${s}')`
  }
  
  const joinedArgs = args.join('/') 
  return `url('${ joinedArgs.replace(/\\/g, '/') }')`
}

export function pathJoin(...args) {
  const [f, s] = args
  /*
      data: ,      HTML
    if    
    , f === undefined   contentPath    :)
              . 
  */
  if (!f) { //  && s.indexOf('data:') === 0
    return s
  }
  return args.join('\\')
}
